class ProfileImg extends Component {
  constructor(props){
    super(props);
    this.toggleImg = this.toggleImg.bind(this);
    this.state = {showprofpic: true};
  }
  toggleImg(){
    this.setState({showprofpic: !this.state.showprofpic });
  }
  render() {
    return (
      <div className="about-img" ><img src={this.state.showprofpic ? profileimage2 : profileimage1} onClick={this.toggleImg} alt="me!"/><div className="caption"><small>{this.state.showprofpic ? "click the image for a cooler one!" : null}</small></div></div>
    );
  }
}

